* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.loader {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 100em;
  align-items: center;
  flex-direction: column;
  row-gap: 20px;
  text-align: center;
  align-items: center;
}

.bg {
  background: linear-gradient(45deg, #b87ad3, #90cfec);
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: -2000;
}

.bg-area li {
  position: absolute;
  list-style: none;
  bottom: -150px;
  left: 5%;
  font-size: 5rem;
  animation: spin 20s linear infinite;
}

.bg-area li:nth-child(2) {
  left: 10%;
  animation-delay: 2s;
  animation-duration: 15s;
}

.bg-area li:nth-child(3) {
  left: 20%;
  animation-delay: 4s;
}

.bg-area li:nth-child(4) {
  left: 30%;
  animation-delay: 2s;
}

.bg-area li:nth-child(5) {
  left: 40%;
  animation-delay: 2s;
  animation-duration: 30s;
}

.bg-area li:nth-child(6) {
  left: 50%;
  animation-delay: 6s;
}

.bg-area li:nth-child(7) {
  left: 60%;
  animation-delay: 3s;
}

.bg-area li:nth-child(8) {
  left: 70%;
  animation-delay: 4s;
  animation-duration: 25s;
}

.bg-area li:nth-child(9) {
  left: 80%;
  animation-delay: 8s;
  animation-duration: 30s;
}

.bg-area li:nth-child(10) {
  left: 90%;
  animation-delay: 1s;
}

@keyframes spin {
  100% {
    transform: translateY(-800px) rotate(360deg);
    opacity: 0;
  }
}

.container {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 100em;
  align-items: center;
  flex-direction: column;
  row-gap: 20px;
}

.main {
  max-height: 50em;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
  border-bottom-right-radius: 1em;
  background-color: #7d869c;
}

.topSide {
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
}

.concSide {
  max-height: 38.5em;
  overflow: hidden;
  overflow-y: scroll;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
  border-bottom-right-radius: 1em;
  margin-left: 1em;
  margin-right: 1em;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #555;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.search-wrapper {
  padding-top: 1em;
  padding-bottom: 1em;
  text-align: center;
}

.listEmoji:hover .copyEmoji {
  display: block;
}

.listEmoji:hover {
  background: #555;
  display: block;
}

.listEmoji {
  display: flex;
  border-radius: 1em;
  background: #a2abab;
  height: 4em;
  margin-bottom: 1em;
  cursor: pointer;
}

.copyEmoji {
  display: none;
  justify-content: space-between;
  line-height: 4em;
  float: right;
  text-align: center;
  margin-right: 1em;
  color: rgba(0, 0, 0, 0.5);
}

.showEmoji {
  float: left;
  justify-content: space-between;
  padding: 1em;
}

.showEmojiName {
  line-height: 4em;
  justify-content: space-between;
  float: left;
  text-align: center;
}

h1 {
  padding-top: 1em;
  text-align: center;
  align-items: center;
}

input {
  width: 50em;
  height: 3em;
  border-style: hidden;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
  border-bottom-right-radius: 1em;
  padding-left: 1em;
  padding-right: 1em;
}
